@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;700;800;900&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&display=swap);

:root {
  --background-color:#050505;
  --foreground-color: #F2F2F2;  
  --foreground-grey: #909090;
  font-family: Inter;
}

* {
  margin:0px;
  padding:0px;
}

body {
  background-color: #050505;
  background-color: var(--background-color);
  color: #F2F2F2;
  color: var(--foreground-color);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.nav {
    font-size: 12px;
    font-weight: normal;
    letter-spacing: 2px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content:space-between;
            justify-content:space-between;
    padding: 40px 140px;
}

.nav b {
    font-weight: 600;
}

/* .nav b:hover {
    font-weight: 100;
} */

.nav ul {
    list-style: none;
    display: inline;
}

.nav li {
    display: inline-block;
    padding-left: 40px;
}

.nav a {
    color:var(--foreground-color);
    text-decoration: none;
    transition: all 0.3s ease-in;
}

.nav a:hover {
    color:var(--foreground-grey);
}
.home {
    padding-top: 50px;
    text-align: center;
    font-size: 14px;
    font-weight: 200;
}
.archieve-wrap{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.archieve{
    width: 1000px;
}
.archieve h1 {
    font-size: 38px;
    line-height: 48px;
    letter-spacing: 0.08em;
    font-weight: 100;
}

.archieve h2 {
    font-size: 28px;
    line-height: 48px;
    letter-spacing: 0.08em;
    font-weight: 100;
}

.archieve img {
    display: block;
    width: 1000px;
}

.archieve ul {
    
    font-size: 13px;
    list-style:disc;
}

.archieve p {
    font-family: 'Inter', sans-serif;
    font-size: 14px;
    line-height: 21px;
}


/* From markdown */
.appendix {
    color:red;
}

